.catalog {
  min-width: (4 * $cards-size-width)
    + (3 *($cards-margin-rights) + $min-padding-left-right);

  max-width: (4* $cards-size-width)
    + (3 *($cards-margin-rights) + $max-padding-left-right);

  margin: 0 auto;
  display: flex;
  padding: 50px 41px;
  flex-wrap: wrap;

  &__card {
    margin-right: $cards-margin-rights;
    margin-bottom: 46px;
    &:nth-child(4n) {
      margin-right: 0;
    }
  }
}
